<template>
  <div class="faxian">
    <topnav :p="p"></topnav>
    <jingxuanshipin :pic8="pc1"></jingxuanshipin>
    <headers style="padding:0.5rem;" :pc2="pc2"></headers>
    <titler style="padding:0.5rem;" :pp="pp"></titler>
    <tuijian style="padding:0.5rem;"></tuijian>
    <music></music>
    <bar></bar>

  </div>
</template>

<script>
import topnav from "../first/components/topnav";
import jingxuanshipin from "../first/components/jingxuanshipin";
import headers from "./components/headers";
import titler from "../first/components/titler";
import tuijian from "./components/tuijian";
import music from '../music';
import bar from '../quanju';

export default {
  name:"faxian",
  data() {
    return {
      pp: "推荐",
      p: "发现",
      //   头部视频
      pc1: [],
      //   照片
      pc2: []
    };
  },
  components: { topnav, jingxuanshipin, headers, titler , tuijian , bar , music },
  methods: {
    getp() {
      this.axios.get("/getfaxian").then(res => {
        var result = res.data;
        for (let i in result) {
          // 这是轮播的照片
          if (i < 6) {
            this.pc1.push(result[i]);
          } else if (i < 20) {
            this.pc2.push(result[i]);
          } else {
            return;
          }
        }
      });
    }
  },
  created() {
    this.getp();
  }
};
</script>

<style scoped>
.faxian {
  margin-bottom:6rem;
}
</style>
