<template>
  <div class="headers">
    <md-card-media>
      <!-- swiper -->
      <swiper :options="swiperOption">
        <swiper-slide class="img" v-for="(p,i) in pc2" :key="i">
          <img style="width:100%;" v-lazy="'http://127.0.0.1:3000'+p.pic" alt>
          <p>{{p.content}}</p>
        </swiper-slide>
      </swiper>
    </md-card-media>
  </div>
</template>

<script>
export default {
  name:"headers",
  data() {
    return {
      swiperOption: {
        slidesPerView: "auto",
        spaceBetween: 30,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      }
    };
  },
  props: ["pc2"],
  created() {
  }
};
</script>

<style scoped>
.img{
  width: 3rem;
  border-radius: 50%;
}
.img>img{
  border:1px solid #f00;
  border-radius:50%;
}
img[lazy=loading] {
  width: 100%;
  height: 50px;
  margin: auto;
}
</style>
