<template>
  <div class="tuijian">
    <div v-for="(v,i) in videos" :key="i" class="quanju">
      <span style="float:left;">{{v.scontent}}</span>
      <div class="videos">
        <video
          controls
          :poster="'http://127.0.0.1:3000'+v.simg"
          class="viddo"
          style="width:100%;border-radius: 0.5rem;"
          v-lazy="'http://127.0.0.1:3000'+v.svideo"
        ></video>
      </div>
      <div class="xiaoxi_ju">
        <p style="float:left;">{{v.sn}}</p>
        <div class="xiaoxi">
          <div class="xiao_xi" @click="fayan">
            <img style="width:100%;" src="../../../assets/icon/xinxi.png" alt />
          </div>
          <p>{{content}}</p>
          <div class="xiao_xi" @click="add($event)">
            <img style="width:100%;margin-top:-0.2rem;" :src="src" alt />
          </div>
          <p>{{v.n}}</p>
        </div>
      </div>
      <div style="display:flex;">{{v.cn}}</div>
      <div v-show="isactive">
        <div>
          <input id="i" v-focus type="text" />
          <button @click="sure(v.id)">确认</button>
        </div>
      </div>
    </div>
    <mt-button type="default" size="large" @click="getmore">加载更多</mt-button>
  </div>
</template>

<script>
import { Indicator } from "mint-ui";
export default {
  name: "tuijian",
  data() {
    return {
      pno: 1,
      ps: 4,
      videos: [],
      src: require("../../../assets/icon/dianzan1.png"),
      istrue: false,
      n: 0,
      isactive: false
    };
  },
  created() {
    this.geter();
    this.getes();
  },
  methods: {
    sure(ids) {
      let i = document.getElementById("i");
      let cn = i.value;
      let id = ids;
      // 这里需要改为插入数据库
      let params = { cn, id };
      this.axios.get("/videoscn", { params }).then(res => {
        if (res.data.code == 1) {
          this.isactive = false;
        }
      });
    },
    fayan() {
      this.isactive = true;
    },
    add(e) {
      if (this.istrue == false) {
        e.target.src = require("../../../assets/icon/dianzan2.png");
        this.n++;
        this.istrue = true;
      } else {
        e.target.src = require("../../../assets/icon/dianzan1.png");
        this.n--;
        this.istrue = false;
      }
    },
    getes() {
      var a = Math.floor(Math.random() * 300);
      this.n = a;
    },
    getmore() {
      this.pno++;
      let pno = this.pno;
      let ps = this.ps;
      let params = { pno, ps };
      this.axios.get("/videos", { params }).then(res => {
        let data = res.data;
        for (let key of data) {
          if (!key) {
            return;
          } else {
            Indicator.open({
              text: "努力加载中...",
              spinnerType: "double-bounce"
            });
            this.videos.push(key);
            setTimeout(() => {
              Indicator.close();
            }, 1000);
          }
        }
      });
    },
    geter() {
      let pno = this.pno;
      let ps = this.ps;
      let params = { pno, ps };
      this.axios.get("/videos", { params }).then(res => {
        var result = res.data;
        for (var key of result) {
          this.videos.push(key);
        }
      });
    }
  }, // 这是自定义的指令,自动获取焦点
  directives: {
    focus: {
      inserted(elem) {
        elem.focus();
      }
    }
  }
};
</script>

<style scoped>
.quanju {
  width: 100%;
  margin-bottom: 4rem;
}
.videos {
  width: 100%;
  border-radius: 0.5rem;
}
.xiaoxi_ju {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
}
.xiaoxi {
  display: flex;
  align-items: space-between;
  margin-right: 1rem;
}
.xiaoxi > p {
  margin-top: 0.3rem;
}
.xiao_xi {
  width: 1.8rem;
  height: 1.8rem;
  display: flex;
  align-items: center;
  margin: 0 0.5rem;
}
img[lazy=loading] {
  width: 100%;
  height: 100px;
  margin: auto;
}
</style>
